.App {
  margin: auto;
  width: 50%
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.Remove-icon,
.Edit-icon,
.Add-icon {
  background-color: white !important;
  padding-left: 2px;
  padding-right: 2px;
  height: 19px;
  border-radius: 3px;
  border-width:1px
}

.Add-icon {
  margin-left: 5px;
}

.Input {
  width: 100%;
  height: 25px;
  border-color: gray;
  border-radius: 5px;
  border-width: 1px
}

.Add-btn-outline {
  text-align: right;
  width: 15%;
  font-weight: bold;
  justify-content: center;
  margin-top: 4px
}